html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@font-face {
  font-family: AvenirNext-Regular;
  src: url("fonts/AvenirNext-Regular.woff2") format("woff2"), url("fonts/AvenirNext-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: AvenirNext-DemiBold;
  src: url("fonts/AvenirNext-DemiBold.woff2") format("woff2"), url("fonts/AvenirNext-DemiBold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

html {
  font-size: 74%;
}

@media only screen and (max-width: 767px) {
  html {
    font-size: 62.5%;
  }
}

body {
  min-width: 320px;
  background: #2C3235;
  font-family: 'AvenirNext-Regular', serif;
  font-size: 1.3rem;
  color: #5D90A3;
  letter-spacing: .076rem;
}

.center {
  max-width: 50rem;
  margin: 0 auto;
  padding: 0 2.7rem;
}

@media only screen and (max-width: 767px) {
  .center {
    padding: 0 1.5rem;
  }
}

.header {
  padding: 1rem 0;
  background: #5D90A3;
}

.logo {
  width: 6rem;
  height: 2.6rem;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
  background-size: contain;
}

.container {
  padding: 2.6rem 0;
}

.title {
  margin-bottom: 1.8rem;
  font-size: 1.6rem;
  color: white;
}

.title_custom {
  font-family: 'AvenirNext-DemiBold', serif;
  color: #72BA8A;
}

.title + .text {
  margin-top: -.7rem;
}

.text {
  margin-bottom: 1.5rem;
}

.text p {
  margin-bottom: 1.8rem;
}

.counter {
  font-family: 'AvenirNext-DemiBold', serif;
  color: #5D90A3;
}

.comments {
  margin-bottom: 3.5rem;
}

.comments__item {
  margin-bottom: 1.6rem;
  padding: 1.2rem .7rem;
  background: white;
  box-shadow: 0 0.3rem 1rem 0.1rem rgba(0, 0, 0, 0.06);
  border-radius: .2rem;
}

.comments__head {
  display: flex;
  margin-bottom: .6rem;
  font-family: 'AvenirNext-DemiBold', serif;
  font-size: .8rem;
  color: #2C3235;
  letter-spacing: 0.01rem;
  line-height: 1.25;
}

.comments__meta {
  min-height: 1.5rem;
  padding: .3rem 0 .3rem 2.3rem;
  background-repeat: no-repeat;
  background-position: 0 50%;
  background-size: 1.5rem 1.5rem;
}

.comments__meta.happy {
  background-image: url(data:image/svg+xml;base64,);
}

.comments__meta.sad {
  background-image: url(data:image/svg+xml;base64,);
}

.comments__location {
  min-height: 1.5rem;
  margin-left: auto;
  padding: .3rem 2.3rem .3rem 0;
  background-repeat: no-repeat;
  background-position: 100% 50%;
  background-image: url(data:image/svg+xml;base64,);
  background-size: 1.5rem 1.5rem;
  font-family: 'AvenirNext-DemiBold', serif;
  font-size: 8px;
  color: black;
  letter-spacing: 0.015rem;
}

.comments__body {
  padding: 1.4rem 1.2rem;
  background: #F8F8F8;
  border-radius: .2rem;
  font-size: 1.3rem;
  color: #2C3235;
  letter-spacing: 0.018rem;
}

.hr {
  margin: 3.5rem 0 3.2rem;
  height: 1px;
  background: #979797;
}

.btn {
  display: inline-block;
  min-width: 13.8rem;
  padding: 1rem 2.3rem;
  background: #72BA8A;
  border: 1px solid #72BA8A;
  border-radius: .2rem;
  font-family: 'AvenirNext-DemiBold', serif;
  font-size: 1.3rem;
  color: #FFFFFF;
  letter-spacing: .072rem;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .btn {
    min-width: 0;
    padding: 1rem 1.5rem;
  }
}

.btn_sm {
  padding: .7rem 2rem;
  border-radius: .3rem;
  font-size: 1rem;
  letter-spacing: .076rem;
}

.btn_red {
  background: #B86578;
  border-color: #B86578;
}

.buttons {
  display: flex;
  margin-bottom: 3.3rem;
}

.buttons .btn:not(:last-child) {
  margin-right: 1.5rem;
}

.form {
  margin-bottom: 5rem;
}

.field {
  display: flex;
  margin-bottom: 3.6rem;
  align-items: center;
}

.field__label {
  margin-right: 2.7rem;
  font-size: 1.3rem;
  color: #5D90A3;
  letter-spacing: .076rem;
}

.field__wrap {
  flex: 1;
}

.field__input {
  width: 100%;
  height: 3.8rem;
  padding: 0 1.2rem;
  border: 1px solid #95989A;
  border-radius: .2rem;
  font-family: 'AvenirNext-Regular', serif;
  font-size: 1.6rem;
  color: #2C3235;
  letter-spacing: 0.01rem;
}

.router {
  margin-bottom: 3rem;
}

.router__item {
  display: flex;
  margin-bottom: 1.2rem;
  align-items: center;
}

.router__col {
  font-size: 1.5rem;
  color: #FFFFFF;
  letter-spacing: .076rem;
}

.router__col:first-child {
  margin-right: 2.2rem;
  flex: 0 0 8.6rem;
}

.router .btn {
  min-width: 0;
  width: 100%;
}

.section {
  margin-bottom: 3.2rem;
}

.toggle {
  position: relative;
  user-select: none;
  cursor: pointer;
}

.toggle__input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.toggle__input:checked + .toggle__wrap .toggle__text:first-child {
  color: #D8D8D8;
}

.toggle__input:checked + .toggle__wrap .toggle__text:last-child {
  color: #72BA8A;
}

.toggle__input:checked + .toggle__wrap .toggle__line:before {
  background: #72BA8A;
  transform: translateX(3.1rem);
}

.toggle__wrap {
  display: flex;
  align-items: center;
}

.toggle__preview {
  display: flex;
  align-items: center;
  flex: 0 0 12.9rem;
}

.toggle__text {
  font-family: 'AvenirNext-DemiBold', serif;
  font-size: .7rem;
  text-transform: uppercase;
  color: #D8D8D8;
  transition: color .2s;
}

.toggle__text:first-child {
  color: #B86578;
}

.toggle__line {
  position: relative;
  height: .7rem;
  margin: 0 .5rem;
  background: #FFFFFF;
  border: 1px solid #D8D8D8;
  border-radius: .3rem;
  flex: 0 0 5rem;
}

.toggle__line:before {
  content: '';
  position: absolute;
  top: -.8rem;
  left: -.2rem;
  width: 2.1rem;
  height: 2.1rem;
  background: #B86578;
  border-radius: 50%;
  box-shadow: 0 0.1rem 0.2rem 0 rgba(0, 0, 0, 0.5);
  transition: transform .2s;
}

.toggle__content {
  font-size: 1.5rem;
  color: white;
  letter-spacing: .076rem;
}

.switch {
  display: block;
  position: relative;
  margin-bottom: 1.5rem;
}

.switch__input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.switch__input:checked + .switch__content:after {
  opacity: 1;
}

.switch__content {
  display: block;
  min-height: 2.4rem;
  padding: .3rem 0 .1rem 12.9rem;
  font-size: 1.5rem;
  color: #FFFFFF;
  letter-spacing: .076rem;
  user-select: none;
  cursor: pointer;
}

.switch__content:before, .switch__content:after {
  content: '';
  position: absolute;
}

.switch__content:before {
  top: 0;
  left: 5rem;
  width: 2.4rem;
  height: 2.4rem;
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-size: contain;
}

.switch__content:after {
  top: .3rem;
  left: 5.5rem;
  width: 2rem;
  height: 1.5rem;
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-size: contain;
  opacity: 0;
  transition: opacity .2s;
}

.printer__item {
  margin-bottom: 3rem;
}

.printer__body {
  margin-top: 1.5rem;
}

.printer .text {
  margin-bottom: 2.2rem;
  padding-left: 12.9rem;
  font-size: 1.5rem;
  letter-spacing: .076rem;
}

.auth {
  padding-bottom: 1.5rem;
}

.auth__header {
  display: flex;
  padding: 3.3rem 0 2.3rem;
  justify-content: center;
}

.auth__header .logo {
  width: 8.1rem;
  height: 3.5rem;
}

.auth__preview {
  display: flex;
  height: 18.5rem;
  margin-bottom: 2.2rem;
}

.auth__pic {
  max-width: 29rem;
  margin: auto;
}

.auth__title {
  margin-bottom: 2.7rem;
  font-size: 2.5rem;
  text-align: center;
  color: white;
}

.auth__title span {
  display: block;
  font-family: 'AvenirNext-DemiBold', serif;
  color: #FF0000;
}

.auth__form {
  max-width: 36rem;
  margin: 0 auto 3.9rem;
}

.auth__field {
  margin-bottom: 1.25rem;
}

.auth__field .field__input {
  height: 5.22rem;
  text-align: center;
}

.auth__help {
  margin: 4rem 0 6.7rem;
  color: white;
  text-align: center;
}

.auth__help a {
  font-family: 'AvenirNext-DemiBold', serif;
  color: white;
  text-decoration: none;
}

.auth__btn {
  display: block;
  width: 31.2rem;
  margin: 3.1rem auto 0;
}

.auth_error .auth__pic {
  max-width: 38.75rem;
}

.auth .text {
  text-align: center;
}
