@mixin d {
  @media only screen and (max-width: "1120px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@include font(AvenirNext-Regular, AvenirNext-Regular);
@mixin f {
  font-family: 'AvenirNext-Regular', serif; }
@include font(AvenirNext-DemiBold, AvenirNext-DemiBold);
@mixin fb {
  font-family: 'AvenirNext-DemiBold', serif; }

html {
  font-size: 74%;
  // font-size: 62.5%
  @include m {
    font-size: 62.5%; } }

body {
  min-width: 320px;
  background: #2C3235;
  @include f;
  font-size: 1.3rem;
  color: #5D90A3;
  letter-spacing: .076rem; }

.center {
  max-width: 50rem;
  margin: 0 auto;
  padding: 0 2.7rem;
  @include m {
    padding: 0 1.5rem; } }

.header {
  padding: 1rem 0;
  background: #5D90A3; }

.logo {
  width: 6rem;
  height: 2.6rem;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
  background-size: contain; }

.container {
  padding: 2.6rem 0; }

.title {
  margin-bottom: 1.8rem;
  font-size: 1.6rem;
  color: white;
  &_custom {
    @include fb;
    color: #72BA8A; } }

.title + .text {
  margin-top: -.7rem; }

.text {
  margin-bottom: 1.5rem;
  p {
    margin-bottom: 1.8rem; } }

.counter {
  @include fb;
  color: #5D90A3; }

.comments {
  margin-bottom: 3.5rem;
  &__item {
    margin-bottom: 1.6rem;
    padding: 1.2rem .7rem;
    background: white;
    box-shadow: 0 .3rem 1rem .1rem rgba(0,0,0,0.06);
    border-radius: .2rem; }
  &__head {
    display: flex;
    margin-bottom: .6rem;
    @include fb;
    font-size: .8rem;
    color: #2C3235;
    letter-spacing: 0.01rem;
    line-height: 1.25; }
  &__meta {
    min-height: 1.5rem;
    padding: .3rem 0 .3rem 2.3rem;
    background-repeat: no-repeat;
    background-position: 0 50%;
    background-size: 1.5rem 1.5rem;
    &.happy {
      background-image: url(data:image/svg+xml;base64,); }
    &.sad {
      background-image: url(data:image/svg+xml;base64,); } }
  &__location {
    min-height: 1.5rem;
    margin-left: auto;
    padding: .3rem 2.3rem .3rem 0;
    background-repeat: no-repeat;
    background-position: 100% 50%;
    background-image: url(data:image/svg+xml;base64,);
    background-size: 1.5rem 1.5rem;
    @include fb;
    font-size: 8px;
    color: black;
    letter-spacing: 0.015rem; }
  &__body {
    padding: 1.4rem 1.2rem;
    background: #F8F8F8;
    border-radius: .2rem;
    font-size: 1.3rem;
    color: #2C3235;
    letter-spacing: 0.018rem; } }

.hr {
  margin: 3.5rem 0 3.2rem;
  height: 1px;
  background: #979797; }

.btn {
  display: inline-block;
  min-width: 13.8rem;
  padding: 1rem 2.3rem;
  background: #72BA8A;
  border: 1px solid #72BA8A;
  border-radius: .2rem;
  @include fb;
  font-size: 1.3rem;
  color: #FFFFFF;
  letter-spacing: .072rem;
  text-align: center;
  @include m {
    min-width: 0;
    padding: 1rem 1.5rem; }
  &_sm {
    padding: .7rem 2rem;
    border-radius: .3rem;
    font-size: 1rem;
    letter-spacing: .076rem; }
  &_red {
    background: #B86578;
    border-color: #B86578; } }

.buttons {
  display: flex;
  margin-bottom: 3.3rem;
  .btn {
    &:not(:last-child) {
      margin-right: 1.5rem; } } }

.form {
  margin-bottom: 5rem; }

.field {
  display: flex;
  margin-bottom: 3.6rem;
  align-items: center;
  &__label {
    margin-right: 2.7rem;
    font-size: 1.3rem;
    color: #5D90A3;
    letter-spacing: .076rem; }
  &__wrap {
    flex: 1; }
  &__input {
    width: 100%;
    height: 3.8rem;
    padding: 0 1.2rem;
    border: 1px solid #95989A;
    border-radius: .2rem;
    @include f;
    font-size: 1.6rem;
    color: #2C3235;
    letter-spacing: 0.01rem; } }

.router {
  margin-bottom: 3rem;
  &__item {
    display: flex;
    margin-bottom: 1.2rem;
    align-items: center; }
  &__col {
    font-size: 1.5rem;
    color: #FFFFFF;
    letter-spacing: .076rem;
    &:first-child {
      margin-right: 2.2rem;
      flex: 0 0 8.6rem; } }
  .btn {
    min-width: 0;
    width: 100%; } }

.section {
  margin-bottom: 3.2rem; }

.toggle {
  position: relative;
  user-select: none;
  cursor: pointer;
  &__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0; }
  &__input:checked + &__wrap &__text {
    &:first-child {
      color: #D8D8D8; }
    &:last-child {
      color: #72BA8A; } }
  &__input:checked + &__wrap &__line {
    &:before {
      background: #72BA8A;
      transform: translateX(3.1rem); } }
  &__wrap {
    display: flex;
    align-items: center; }
  &__preview {
    display: flex;
    align-items: center;
    flex: 0 0 12.9rem; }
  &__text {
    @include fb;
    font-size: .7rem;
    text-transform: uppercase;
    color: #D8D8D8;
    transition: color .2s;
    &:first-child {
      color: #B86578; } }
  &__line {
    position: relative;
    height: .7rem;
    margin: 0 .5rem;
    background: #FFFFFF;
    border: 1px solid #D8D8D8;
    border-radius: .3rem;
    flex: 0 0 5rem;
    &:before {
      content: '';
      position: absolute;
      top: -.8rem;
      left: -.2rem;
      width: 2.1rem;
      height: 2.1rem;
      background: #B86578;
      border-radius: 50%;
      box-shadow: 0 .1rem .2rem 0 rgba(0,0,0,0.50);
      transition: transform .2s; } }
  &__content {
    font-size: 1.5rem;
    color: white;
    letter-spacing: .076rem; } }

.switch {
  display: block;
  position: relative;
  margin-bottom: 1.5rem;
  &__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0; }
  &__input:checked + &__content {
    &:after {
      opacity: 1; } }
  &__content {
    display: block;
    min-height: 2.4rem;
    padding: .3rem 0 .1rem 12.9rem;
    font-size: 1.5rem;
    color: #FFFFFF;
    letter-spacing: .076rem;
    user-select: none;
    cursor: pointer;
    &:before,
    &:after {
      content: '';
      position: absolute; }
    &:before {
      top: 0;
      left: 5rem;
      width: 2.4rem;
      height: 2.4rem;
      background-image: url(data:image/svg+xml;base64,);
      background-repeat: no-repeat;
      background-size: contain; }
    &:after {
      top: .3rem;
      left: 5.5rem;
      width: 2rem;
      height: 1.5rem;
      background-image: url(data:image/svg+xml;base64,);
      background-repeat: no-repeat;
      background-size: contain;
      opacity: 0;
      transition: opacity .2s; } } }

.printer {
  &__item {
    margin-bottom: 3rem; }
  &__body {
    margin-top: 1.5rem; }
  .text {
    margin-bottom: 2.2rem;
    padding-left: 12.9rem;
    font-size: 1.5rem;
    letter-spacing: .076rem; } }

.auth {
  padding-bottom: 1.5rem;
  &__header {
    display: flex;
    padding: 3.3rem 0 2.3rem;
    justify-content: center;
    .logo {
      width: 8.1rem;
      height: 3.5rem; } }
  &__preview {
    display: flex;
    height: 18.5rem;
    margin-bottom: 2.2rem; }
  &__pic {
    max-width: 29rem;
    margin: auto; }
  &__title {
    margin-bottom: 2.7rem;
    font-size: 2.5rem;
    text-align: center;
    color: white;
    span {
      display: block;
      @include fb;
      color: #FF0000; } }
  &__form {
    max-width: 36rem;
    margin: 0 auto 3.9rem; }
  &__field {
    margin-bottom: 1.25rem;
    .field__input {
      height: 5.22rem;
      text-align: center; } }
  &__help {
    margin: 4rem 0 6.7rem;
    color: white;
    text-align: center;
    a {
      @include fb;
      color: white;
      text-decoration: none; } }
  &__btn {
    display: block;
    width: 31.2rem;
    margin: 3.1rem auto 0; }
  &_error &__pic {
    max-width: 38.75rem; }
  .text {
    text-align: center; } }

